.main{
    padding: 10px;
    height: 100vh;
}

.layout{
    display: flex;
}

.sidebar{
    background-color: #006D5B;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    min-height: 100%;
    padding: 10px;
    color: white;
    width : 300px
}
.content{
    width: 100%;
    height: 100%;
 }

 .header{
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-bottom: 20px;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-between
}

.body {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    height: 82vh;
    width: 100%;
    padding: 15px;
}

.logo h6{
    font-size: 1rem;
    text-align: center;
    margin: 20px 0px ;
}

.menu{
    margin-top: 100px;
    padding: 0 10px;
}

.menu-item{
    margin-top: 30px;
}

.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 18px;
}
.menu-item i{
    text-decoration: none;
    font-size: 18px;
    margin:0 15px;
}

.active {
    
    background-color: #013737;
    padding: 5px;
    border-radius: 5px;
}

.active a, .active i{
    color: white;
}

.header-content{
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}

.header-content i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.header-content a{
    text-decoration: none;
    font-size: 16px;
    color: black;
    margin: 0 10px;
    text-transform: uppercase;
}

/* Media query for mobile devices */
@media screen and (max-width: 1024px) {
    /* Adjust styles for smaller screens */

    .layout {
        flex-direction: column; /* Stack columns on top of each other on smaller screens */
    }
    .body {
        height: auto; /* Adjust height for smaller screens */
    }

    .header {
        height: auto; /* Adjust height for smaller screens */
    }

    .header-content {
        margin-top: 20px;
        justify-content: center;
    }
    
    .sidebar {
        width: 100%; /* Make the sidebar full-width on smaller screens */
        margin-right: 0; /* Remove margin on smaller screens */
    }

    .menu {
        margin-top: 20px; /* Adjust margin for smaller screens */
    }

    h1 {
        font-size: 24px; /* Adjust the font size for h1 on smaller screens */
    }

    h2 {
        font-size: 22px; /* Adjust the font size for h2 on smaller screens */
    }

    h3 {
        font-size: 20px; /* Adjust the font size for h3 on smaller screens */
    }

    h4 {
        font-size: 18px; /* Adjust the font size for h4 on smaller screens */
    }

    h5 {
        font-size: 16px; /* Adjust the font size for h5 on smaller screens */
    }

    h6 {
        font-size: 14px; /* Adjust the font size for h6 on smaller screens */
    }
}